<template>
    <div>
      <div class="header">
        <router-link tag="div" to="/admin">
          <Search></Search>
        </router-link>
        <Banner :banners="banners"></Banner>
        <QuickEntrance :quickEntrance="quickEntrance"></QuickEntrance>
      </div>
      <Type :emojis="emojis" :typeItem="typeItem"></Type>
      <div class="test">
        <GoodsList></GoodsList>
      </div>
      <!-- <Footer></Footer> -->
    </div>
</template>

<script>
  import axios from 'axios'
  import Search from '@/components/search/search'
  import Banner from '@/components/banner/banner'
  import QuickEntrance from '@/components/quick-entrance/QuickEntrance'
  import Type from '@/components/type/type'
  import GoodsList from '@/components/goods-list/GoodsList'
  import Footer from '@/components/footer/footer'
  
  export default {
    name:'Home',
    components: {
      Search,
      Banner,
      QuickEntrance,
      Type,
      GoodsList,
      Footer
    },
    data () {
      return {
        emojis:[],
        typeItem:[],
        banners:[],
        quickEntrance:[]
      };
    },
    methods:{
      getHomeData(){
        axios.get('http://localhost:3333/data/getData')
        .then(this._getHomeData);
      },
      _getHomeData(res){
        console.log("res",res)
        this.emojis = res.data.emojis
        this.typeItem = res.data.typeItem
        this.banners = res.data.banners
        this.quickEntrance = res.data.quickEntrance
      }
    },
    mounted(){
      this.getHomeData();
    }

  }

</script>
<style lang='stylus' scoped>
  .header
      top 0
      left 0
      background-color #ff7700
  .test
    padding-bottom 50px
    width 100%


</style>


